<template>
  <div>
    <main> <!-- Début du main -->
      <div class="title"> <!-- title de présentation, dans le main -->
        <h1>Biographies</h1>
      </div>
      <div class="card content"><!-- intro -->
        <center v-if="jwt"><img src="@/assets/icon/edit.svg" class="action" @click="$router.push('/edit/intro')"></center>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="introLoading"></BarLoader>
        <div class="content">
          <viewer :value="getIntro.content"/>
        </div>
      </div> <!-- fin intro -->
      <Search/>
      <div class="card persons">
        <h2>Auteurs:</h2>
        <div class="gallery"> <!-- ici toutes les biographies -->
          <BarLoader style="margin: 0 auto; border-radius:5px" v-if="personsLoading"></BarLoader>
          <Person v-for="person in getPersons" v-bind="person" :key="person.id"></Person>
          <NewCard type="person"></NewCard>
        </div> <!--fin class biographies -->
      </div>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import NewCard from '@/components/card/new.vue'
import Person from '@/components/card/person.vue'
import Search from '@/components/search/card.vue'
import { mapActions, mapGetters } from 'vuex'
import 'tui-editor/dist/tui-editor-contents.css';
import 'highlight.js/styles/github.css';
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'

export default {
  name: 'home',
  components: {
    Person,
    NewCard,
    Search,
    Viewer
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.fetchPersons()
    this.clearPerson()
    this.clearArtwork()
    this.fetchIntro()
  },
  computed: {
      ...mapGetters('persons', [
        'getPersons',
        'personsLoading'
      ]),
      ...mapGetters('jwt', [
        'jwt'
      ]),
      ...mapGetters('intro', [
        'getIntro',
        'introLoading'
      ])
  },
  methods: {
    ...mapActions('persons', [
      `fetchPersons`,
      'clearPerson'
    ]),
    ...mapActions('alert', [
      'newAlert'
    ]),
    ...mapActions('artworks', [
      'clearArtwork',
    ]),
    ...mapActions('intro', [
      'fetchIntro',
    ])
  }
}
</script>
