<template>
    <div class="element" itemscope itemtype="http://schema.org/Thing">
        <div class="head" itemprop="name" @click="expand = !expand">
            <img target="blank" class="image" v-if="type=='person'" :src="img" itemprop="image" :alt="'image de '+name" @error="imageUrlAlt">
            <div class="info">
                <div class="type">
                    {{type=="artwork"?"œuvre":"auteur/trice"}} <StateDisplay :state="state"/>
                </div>
                <span class="name">{{name}}</span>
                <span class="date" v-if="date"> ({{date}})</span>
                <span class="author" v-if="person"> de {{person}}</span>
            </div>
            <div class="actions">
                <span @click="restoreElement(id)" class="restore" v-if="showActions">Restaurer</span>
                <span @click="deleteElement(id)" class="delete" v-if="showActions">Supprimer</span>
                <router-link v-else :to="getLink" class="see" >Voir</router-link>
            </div>
            <div class="expand">
                <img src="@/assets/icon/down.svg" :class="{'on': expand}">
            </div>
        </div>
        <div class="content" v-if="expand">
            <viewer :value="bio" />
            <viewer :value="content" />
        </div>
    </div>
</template>
<script>
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'
import { mapActions, mapGetters } from 'vuex'
import StateDisplay from '@/components/state/Display.vue'

export default {
    components: {
        Viewer,
        StateDisplay
    },
    props: ['id','type','slug','state', 'name','img','bio', 'person', 'content', 'date', 'actions'],
    data () {
        return {
            expand: false
        }
    },
    computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        showActions: function () {
            return this.jwt && this.actions == "true"
        },
        getLink: function () {
            if (this.type=='person') return '/'+this.slug
            else if (this.type=='artwork') return '/'+this.person+'/'+this.slug
            else return '/'
        }
    },
    methods: {
        ...mapActions('trash',[
            `deleteElement`,
            `restoreElement`
        ]),
        imageUrlAlt(event) {
            if (event.target.alt != "Image introuvable") {
                event.target.src = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT3ULvHWF2iEicQptWiXWmdUJRHqpkwrvLt9s_SA34UNQucor3KXg&s"
                event.target.alt = "Image introuvable"
            }
        }
    }
}
</script>