<template>
    <div class="box" :style="'width:'+width">
        <div class="options">
            <input type="checkbox" id="searchPersons" v-model="searchPersons">
            <label for="searchPersons">Auteurs<img height="15px" src="@/assets/icon/check.svg"></label>
            <input type="checkbox" id="searchArtworks" v-model="searchArtworks">
            <label for="searchArtworks">Œuvres<img height="15px" src="@/assets/icon/check.svg"></label>
        </div>
        <input placeholder="Rechercher..." v-model="research" class="input" @keyup.enter="search()"/>
        <img src="@/assets/icon/search.svg" @click="search()" class="search" v-if="!searching">
        <MoonLoader :size=20 v-if="searching" class="search"></MoonLoader>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    props: {
        width: {
            type: String,
            default: "auto"
        }
    },
    data () {
        return {
            searchPersons: false,
            searchArtworks: true,
            research: '',
            results: [],
            searching: false
        }
    },
    computed: {
        ...mapGetters('persons',[
            'getPersons'
        ]),
        ...mapGetters('artworks', [
            'getArtworks'
        ])
    },
    methods: {
        ...mapActions('artworks',[
            'fetchArtworks',
        ]),
        search() {
            if (!this.searchArtworks && !this.searchPersons) {
                this.searchPersons = true
                this.searchArtworks = true
            }
            this.results = []
            this.searching = true
            if (this.research !== "" && this.research !== " ") {
                this.research = this.research.toLowerCase()
                this.fetchArtworks()
                .then(()=> {
                    let persons = this.getPersons;
                    let artworks = this.getArtworks;
                    if (this.searchPersons) {
                        for (let i = 0; i < persons.length; i++) {
                            try {
                                if (persons[i].name.toLowerCase().includes(this.research) || persons[i].slug.toLowerCase().includes(this.research) || this.persons[i].date.toLowerCase().includes(this.research)) {
                                    this.results.push(persons[i])
                                }
                            } catch {
                                continue
                            }
                        }
                    }
                    if(this.searchArtworks) {
                        for (let i = 0; i < artworks.length; i++) {
                            try {
                                if (artworks[i].name.toLowerCase().includes(this.research) || artworks[i].slug.toLowerCase().includes(this.research) || artworks[i].date.toLowerCase().includes(this.research)) {
                                    this.results.push(artworks[i])
                                }
                            } catch {
                                continue
                            }
                        }
                    }
                    this.searching = false
                })
            } else {
                this.searching = false
            }
            this.$emit('input', this.results)
        }
    }
}
</script>
<style scoped>
.box {
    display: flex;
    height: 50px;
    border-radius: 5px;
    background: #fff;
}
.options {
    margin: 0 5px;
    user-select: none;
    flex-direction: column;
    text-align: center;
    display: flex;
    justify-content: center;
    border-right: 1px solid #aaa;
    width: 110px;
}
input:checked +label {
    color:#333;
}
input:checked +label img{ 
    opacity: 100;
}

input[type="checkbox"] {display: none;}
label {
    cursor: pointer;
    margin: 2px 0px 2px 10px;
    color: #bbb;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 13px;
    height: 18px;
}
label img {
    margin: 0 0 2px 2px;
    opacity: 0;
}
.input {
    border: none;
    height: 100%;
    width: calc(100% - 50px);
    padding-left: 7px;
    box-sizing: border-box;
    background: none;
    outline: none;
    font-size: 1.1em;
}
div.search {
    margin: 10px;
}
img.search {
    display: inline-block;
    height: 50px;
    width: 50px;
    padding: 12px;
    vertical-align: middle;
    cursor: pointer;
}
img.search:hover {
    transform: scale(1.1)
}
img.search:active {
    transform: scale(0.9)
}
</style>