<template>
    <div class="new" v-if="jwt">
            <img src="@/assets/icon/new.svg" class="action" @click="$router.push('/new/person')" v-if="type== 'person'">
            <li v-if="type== 'artwork'">
                <router-link to="/new/artwork" >
                    Nouvelle œuvre
                    <img src="@/assets/icon/new.svg" class="action">
                </router-link>
            </li>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'


export default {
    props: ['type'],
    computed: {
        ...mapGetters('jwt',[
            'jwt',
            'jwtSubject',
            'jwtIssuer'
        ])
    }
}
</script>