<template>
    <div class="card search">
        <div class="expand" @click="expand= !expand">
                <img src="@/assets/icon/down.svg" :class="{'on': expand}" >
        </div>
        <SearchInput v-model="results" @input="expand = true"/>
        <SearchResults v-if="expand" :results="results" />
    </div>
</template>
<script>
import SearchInput from './input'
import SearchResults from './result'

export default {
    components: {
        SearchInput,
        SearchResults
    },
    data () {
        return {
            results: null,
            expand: true
        }
    },
}
</script>
<style scoped>
.expand {
    float: right;
    display: inline-block;
    padding: 10px;
    height: 50px;
    vertical-align: middle;
    cursor: pointer;
}
.expand > img {
    transition: .3s;
    height: 30px;
    width:auto;
}
.expand > .on {
    transform: rotateZ(-180deg);
}
</style>