<template>
    <span :class="['badge icon', this.passive===undefined?'':'passive']" :title="dic[state]" @click="handleClick()" style="cursor:help">
        <img v-if="state=='beginned'" src="@/assets/icon/state/beginned.svg">
        <img v-if="state=='writing'" src="@/assets/icon/state/writing.svg">
        <img v-if="state=='soon'" src="@/assets/icon/state/soon.svg">
        <img v-if="state=='certif'" src="@/assets/icon/state/certif.svg">
    </span>
</template>
<script>
export default {
    props: ['state', 'passive'],
    data() {
        return {
            dic: {
                beginned: 'Créé il y a peu de temps',
                writing: 'En cours de rédaction',
                soon: 'Bientôt prêt',
                certif: 'Prêt à réviser'
            }
        }
    },
    methods: {
        handleClick() {
            if(this.passive===undefined) {
                this.$router.push('/badges')
            }
        }
    }
}
</script>
